import React from 'react';
import PlayerSelect from './PlayerSelect';
import StrategySelect from './StrategySelect';
import StatusBoard from './StatusBoard';
import TimerBlock from './TimerBlock';

const MODE_PLAYER_SELECT = 1;
const MODE_STRATEGY = 2;
const MODE_STATUS_BOARD = 3;

const SECONDS_IN_MINUTE = 60;
const SECONDS_IN_HOUR = 60 * 60;

const TIMER_NAMES = {
    CURRENT_TURN_TIMER: "turn-timer",
    GAME_TIMER: "game-timer",
    PLAYER_TIMER: "player-timer",
}

class GameManager extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            playerDetails: null,
            gameMode: MODE_PLAYER_SELECT,
            roundNumber: 1,
            totalGameTimer: {
                baseSeconds: 0,
                currentSeconds: 0,
                countStartTime: Date.now(),
                isCounting: true,
            },
            currentTurnTimer: {
                baseSeconds: 0,
                currentSeconds: 0,
                countStartTime: Date.now(),
                isCounting: false,
            },
        };
    }

    //#region Event Handlers
    handleGameStart(playerDetails) {
        this.setState ({
            playerDetails: playerDetails,
            gameMode: MODE_STRATEGY,
        });
        console.log(playerDetails);
    }

    handlePlayerStrategyChange(e, playerNumber) {
        let playerDetails = this.state.playerDetails.slice();
        playerDetails[playerNumber].strategy = e.target.value;
        this.setState ({
            playerDetails: playerDetails,
        });
        console.log("New Strategy is " + playerDetails[playerNumber].strategy + " for index " + playerNumber);
    }

    handleRoundStart() {
        this.setState ({
            gameMode: MODE_STATUS_BOARD,
        });
    }

    handleTurnTimerClicked(time) {
        var timer;
        if (this.state.currentTurnTimer.isCounting) {
            timer = {
                baseSeconds: time,
                isCounting: false,
            }
        }
        else {
            timer = {
                ...this.state.currentTurnTimer,
                isCounting: true,
            }
        }

        this.setState({
            currentTurnTimer: timer
        })
    }

    handleGameTimerClicked(time) {
        var timer;
        if (this.state.totalGameTimer.isCounting) {
            timer = {
                baseSeconds: time,
                isCounting: false,
            }
        }
        else {
            timer = {
                ...this.state.totalGameTimer,
                isCounting: true,
            }
        }

        this.setState({
            totalGameTimer: timer
        })
    }

    handleEndRound() {
        let playerDetails = this.state.playerDetails.slice().map(
            player => ({
                ...player, 
                strategy: null,
            })
        );

        this.setState ({
            gameMode: MODE_STRATEGY,
            roundNumber: this.state.roundNumber + 1,
            playerDetails: playerDetails,
        });
    }
    //#endregion

    //#region Commands
    recalculateTime(timerName, timer) {
        let newTimer = {...timer};

        const time = newTimer.baseSeconds + Math.floor((Date.now() - newTimer.baseTime) / 1000);
        newTimer.currentSeconds = time;

        switch (timerName) {
            case TIMER_NAMES.GAME_TIMER:
                this.setState ({
                    totalGameTimer: newTimer,
                });
                break;
            case TIMER_NAMES.CURRENT_TURN_TIMER:
                this.setState ({
                    currentTurnTimer: newTimer,
                });
                break;
            default:
                break;
                //TODO: player timers
        }

        this.setState({
            currentSeconds: time
        });
    }

    startTimer(timerName) {
        let newTimer;
        switch (timerName) {
            case TIMER_NAMES.GAME_TIMER:
                newTimer = {
                    ...this.state.totalGameTimer,
                    baseTime: Date.now(),
                    isCounting: true,
                }
                this.setState ({
                    totalGameTimer: newTimer,
                });
                this.interval = setInterval(() => this.recalculateTime(timerName, this.state.totalGameTimer), 1000);
                break;
            case TIMER_NAMES.CURRENT_TURN_TIMER:
                newTimer = {
                    ...this.state.currentTurnTimer,
                    baseTime: Date.now(),
                    isCounting: true,
                }
                this.setState ({
                    currentTurnTimer: newTimer,
                });
                this.interval = setInterval(() => this.recalculateTime(timerName, this.state.currentTurnTimer), 1000);
                break;
            default:
                break;
                //TODO: player timers
        }
    }
    //#endregion

    //#region Rendering functions
    renderGameComponent() {
        switch (this.state.gameMode) {
            case MODE_PLAYER_SELECT: 
                return this.renderPlayerSelect();
            case MODE_STRATEGY:
                return this.renderStrategy();
            case MODE_STATUS_BOARD:
                return this.renderStatusBoard();
            default:
                return null;
        }
    }

    renderPlayerSelect() {
        return (
            <div>
                <PlayerSelect onStartGame={playerDetails => this.handleGameStart(playerDetails)}/>
            </div>
        );
    }

    renderStrategy() {
        return (
            <div>
                {this.renderGameHeader(false)}
                <h1>Strategy Phase</h1>
                <StrategySelect 
                    playerDetails={this.state.playerDetails} 
                    onStartRound={() => this.handleRoundStart()}
                    onPlayerStrategyChange={(e, playerNumber) => this.handlePlayerStrategyChange(e, playerNumber)}
                />
            </div>
        );
    }

    renderStatusBoard() {
        return (
            <div>
                {this.renderGameHeader(true)}
                <h1>Status Board</h1>
                <StatusBoard 
                    onEndRound = {() => this.handleEndRound()}
                />
            </div>
        );
    }

    renderGameHeader(showTurnTimer) {
        return <GameHeader
            roundNumber = {this.state.roundNumber}
            totalGameTimer = {this.state.totalGameTimer}
            showTurnTimer = {showTurnTimer}
            currentTurnTimer = {this.state.currentTurnTimer}
            onTurnTimerClick = {(time) => this.handleTurnTimerClicked(time)}
            onGameTimerClick = {(time) => this.handleGameTimerClicked(time)}
        />
    }
    //#endregion

    render() {
        return (
            <div>
                {this.renderGameComponent()}
            </div>
        );
    }
}


function GameHeader(props) {
    let turnTimer = props.showTurnTimer ? 
        <TimerBlock
            id="turnTimer"
            label="Turn Time"
            baseSeconds={props.currentTurnTimer.baseSeconds}
            isCounting={props.currentTurnTimer.isCounting}
            onClick={(time) => props.onTurnTimerClick(time)}
        /> :
        null;

    return (
        <div>
            <label className="timerLabel">{"Round: " + props.roundNumber}</label>
            {turnTimer}
            <TimerBlock
                id="turnTimer"
                label="Total Game Time"
                baseSeconds={props.totalGameTimer.baseSeconds}
                isCounting={props.totalGameTimer.isCounting}
                onClick={(time) => props.onGameTimerClick(time)}
            />
        </div>
    );
}

//-------------------------------------------

function parseTime(totalSeconds) {
    if (!totalSeconds) {
        return "00:00";
    }

    const hours = Math.floor(totalSeconds / SECONDS_IN_HOUR);
    const minutes = Math.floor((totalSeconds % SECONDS_IN_HOUR) / SECONDS_IN_MINUTE);
    const seconds = totalSeconds % SECONDS_IN_MINUTE;

    var time = hours > 0 ? hours + ":" : "";
    time += (minutes < 10 ? "0" + minutes : minutes) + ":";
    time += seconds < 10 ? "0" + seconds : seconds;

    return time;
}

export default GameManager;
